import React from 'react';
import { useParams } from "react-router-dom";

import { Footer, Header, MenuStrip, ScrollToTop } from '../components';

import { IBMExperience, TAExperience, OPSExperience } from '../components/Experiences';

const ExperiencesArray = [
	IBMExperience,
	TAExperience,
	OPSExperience,
];

function ExperienceDetail() {
	const bookmarks = [ ];
	let { id } = useParams();


	if (id >= ExperiencesArray.length) {
		return null;
	}

	const Content = ExperiencesArray[id];

	return (
<div>
<Header/>
<div className="article">
	<MenuStrip bookmarks={bookmarks}/>
	<div className="article-content">
	<Content/>
	</div>
</div>
<Footer/>
<ScrollToTop/>
</div>
	);
}

export { ExperienceDetail };

export default ExperienceDetail;
