import React from 'react';

const flexGroupStyle = {
	display: "flex",
}

function MenuStrip(props) {
	const bookmarks = props.bookmarks ? props.bookmarks.map((bookmark) => {
		return (
		<a className="article-topbar-link" href={`#${bookmark}`}>{bookmark}</a>
		)
	}) : null;

	return (
	<div className="article-topbar">
		<div style={flexGroupStyle}>
		{bookmarks}
		</div>
	</div>
	);
}

export { MenuStrip };
export default MenuStrip;
