import React, { useState, useEffect } from 'react';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faGithub } from '@fortawesome/free-brands-svg-icons';
import { faStar, faCodeBranch, faCircleNotch } from '@fortawesome/free-solid-svg-icons';

const githubSectionStyle = {
	marginTop: "0.4rem",
	marginLeft: "0.4rem",
};

const inline = {
	display: "inline",
}

const ProjectErrorEnum = {
	Loading: 0,
	LimitedReached: 1,
	FetchError: 2,
};

function ProjectHeader(props) {
	const { project } = props;
	const [ projectStars, setProjectStars ] = useState(0);
	const [ projectForks, setProjectForks ] = useState(0);
	const [ projectReady, setProjectReady ] = useState(false);
	const [ projectError, setprojectError ] = useState(ProjectErrorEnum.Loading);

	useEffect(() => {
		const apiUrl = `https://api.github.com/repos/${project.Owner}/${project.RepoName}`;

		fetch(apiUrl, {})
		.then(resp => resp.json())
		.then(json => {
			if (typeof(json.stargazers_count) === "undefined"
					|| typeof(json.forks_count) === "undefined") {
				setprojectError(ProjectErrorEnum.LimitedReached);
				return;
			}
			setProjectStars(json.stargazers_count);
			setProjectForks(json.forks_count);
			setProjectReady(true);
		})
		.catch(err => setprojectError(ProjectErrorEnum.FetchError));

	}, []);

	let githubMetrics;
	if (projectReady) {
		githubMetrics = 
		<div style={inline}>
			<FontAwesomeIcon
				className="article-github-icon"
				icon={faStar}/>{projectStars}
			<FontAwesomeIcon
				className="article-github-icon"
				icon={faCodeBranch}/>{projectForks}
		</div>;
	} else {
		switch (projectError) {
		case ProjectErrorEnum.FetchError:
			githubMetrics = <div>Failed to fetch Github metrics</div>;
			break;
		case ProjectErrorEnum.LimitReached:
			githubMetrics = <div>API rate limit reached.
			I guess there are too many visitors.</div>;
			break;
		case ProjectErrorEnum.Loading:
		default:
			githubMetrics = 
			<div style={inline}>
				<FontAwesomeIcon
					className="article-github-icon"
					icon={faCircleNotch}
					spin
					/>
			</div>;
			break;
		}
	}


	return (
<div>
	<h3 id={project.Name}>{project.Name}</h3>
	<h6>{project.Description}, {project.Language}</h6>

	<div style={githubSectionStyle}>
	<a
	href={project.Url}
	target="_blank"
	rel="noopener noreferrer"
	>
		<FontAwesomeIcon className="article-icon" icon={faGithub}/>
	</a>
	{githubMetrics}
	</div>

	{ project.Tags ? (
	<ul id="tag-list">
		{ project.Tags.map(tag => {
				return <li>{tag}</li>;
			})
		}
	</ul>
	) : null }
	<a
	href={project.PreviewImgUrl}
	rel="noopener noreferrer"
	target="_blank">
		<img className="project_image"
			alt={project.Name}
			src={project.PreviewImgUrl}
		/>
	</a>
</div>
	);

}

export { ProjectHeader };
export default ProjectHeader;
